.body{
    background-color: #F0F2F5;
    font-family: Arial, Helvetica, sans-serif;
    /* margin:0; */
    /* padding:0; */
    /* height:100vh; */
    /* width: 100%; */
    
}
.post-container{
    /* border:1px solid red; */
    width: 600px;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin:auto;
}
    .post{
    border:1px solid lightgray;
    /* height:400px; */
    width: 500px;
    border-radius: 10px;
    padding:20px;
    margin:20px;
    background-color: white;
    box-shadow:0px 0px 10px 2px lightgray;
}
.post .profilepic{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-right:10px;
}
.post .postheader{
    display:flex;
    align-items: center;
    /* border:1px solid red; */
}
.post .postImage{
    width:100%;
    margin-top:10px;
}
.post .postText{
    width:100%;
    margin-top:10px;
}
.post .reaction{
    display:flex;
    align-items: center;
    justify-content: center;
    /* justify-content: space-evenly; */
    /* border:1px solid red; */
    margin-top:10px;
    width:20%;
}
.post .reaction img{
    width:30px;
    height:30px;
}
.post .postfooter{
    display:flex;
    align-items: center;
    justify-content: center;
    justify-content: space-evenly;
    /* border:1px solid red; */
    margin-top:10px;
}
.post .postfooter .icons{
    /* border:1px solid red; */
    width: 100px;
    display:flex;
    align-items: center;
    justify-content: center;
    justify-content: space-evenly;
}
.post .postfooter img{
    width:20px;
    height:20px;
}
.post .comment-main{
    /* border:1px solid red; */
    width: 100%;
    display:flex;
    align-items: center;
    justify-content: center;
    justify-content: space-evenly;
    padding:6px;
}
.comment-div{
    /* border:1px solid red; */
    width: 80%;
    display:flex;
    align-items: center;
    justify-content: center;
    justify-content: space-between;
    height:40px;
    border-radius: 20px;
    /* width:30px; */
    background-color: #F0F2F5;

}
.comment-type{
    background-color: #F0F2F5;

    /* border:1px solid red; */
    width: 38%;
    display:flex;
    align-items: center;
    justify-content: center;
    justify-content: space-evenly;
    margin:20px;
    /* height:40px; */
    /* border-radius: 20px; */
}
.comment-icons{
    background-color: #F0F2F5;
    /* border:1px solid red; */
    width: 30%;
    display:flex;
    align-items: center;
    justify-content: center;
    justify-content: space-evenly;
    margin:20px;
}
.comment-icons img{
    width:20px;
    height:20px;
}